<template>
  <div class="createNewProject">
    <!-- 展示firmiana数据 -->
    <div class="data-firmiana">
      <div class="box-title">Firmiana data display</div>
      <p style="font-style: italic; margin-bottom: 0.9375rem">
        If you check the table, the data will be displayed in the total set. You
        can remove the selected experiment by canceling the check or deleting
        it.
      </p>
      <antdTable
        @changeData="tagData_ipro"
        :from="'iproteome'"
        :messageTags="messageTags"
        :tags="tags"
        key="iproteome"
      ></antdTable>
    </div>
    <!-- 展示local数据 -->
    <div class="data-local">
      <div class="box-title">Custom upload data display</div>
      <p style="font-style: italic; margin-bottom: 0.9375rem">
        If you check the table, the data will be displayed in the total set. You
        can remove the selected experiment by canceling the check or deleting
        it.
      </p>
      <antdTable
        @changeData="tagData_loc"
        :from="'local'"
        :messageTags="messageTags"
        :tags="tags"
        key="local"
      ></antdTable>
    </div>

    <div class="tool-module">
      <!-- 实验数据收集展示 -->
      <p class="box-title">Project data collection and display</p>
      <div class="tag-project">
        <div class="create-tags" style="max-height: 37.5rem; overflow: auto">
          <template v-for="(tag, index) in tags">
            <a-tooltip
              v-if="tag.expname.length > 20"
              :key="tag.expname"
              :title="tag.expname"
            >
              <a-tag
                :key="tag.expname"
                :closable="index == index"
                :color="tag.from === 'local' ? '#4EBABA' : '#5F7EC6'"
                :afterClose="() => handleClose(tag)"
                >{{ `${tag.expname.slice(0, 20)}...` }}</a-tag
              >
            </a-tooltip>
            <a-tag
              v-else
              :key="tag.expname"
              :closable="index == index"
              :color="tag.from === 'local' ? '#4EBABA' : '#5F7EC6'"
              :afterClose="() => handleClose(tag)"
              >{{ tag.expname }}</a-tag
            >
          </template>
        </div>

        <div class="upload-tags">
          <p style="font-style: italic; margin-bottom: 0.9375rem">
            Try to copy &nbsp;
            <span style="background: #f1f1f1"> exp000001, exp000002 </span>
            &nbsp; into the input box , Then hit enter.(Please use English
            comma!)
          </p>
          <div
            class="textarea"
            @click="getFocus"
            style="max-height: 37.5rem; overflow: auto"
          >
            <template v-for="(tag, index) in messageTags">
              <a-tag
                :key="tag"
                :closable="index == index"
                :afterClose="() => handleClose(tag)"
                >{{ tag }}</a-tag
              >
            </template>
            <div class="add-tag">
              <span>{{ messageTagData }}&nbsp;</span>
              <input
                ref="message"
                type="text"
                v-model="messageTagData"
                @keyup.enter="handleMessageData"
                @keyup.delete="handleMessageDelete"
              />
            </div>
          </div>
        </div>
      </div>

      <!-- 添加项目信息，以及提交项目 -->

      <div class="from-exps">
        <div class="box-title">Add project information and submit projects</div>
        <p class="line-text">
          After selecting the lab number, you can add the name and description
          below.
        </p>
        <br />
        <form action id="demo">
          <div class="from-submit">
            <input type="text" placeholder="project Name" v-model="input1" />
            <p class="test">Project name is not repeatable</p>
            <br />
            <textarea
              type="text"
              rows="10"
              placeholder="project Description"
              v-model="input2"
              class="input2"
            />
            <p class="test" @click.prevent="hanldeData">
              Please describe your experiment as much as possible.
            </p>
          </div>
          <div class="progress-bar">
            <div class="bar-inner" @click="submitNewProInfo">
              <!-- <div class="progress" :style="{width: progressWdith+'%'}"></div> -->
              <div class="progress-btn">submit</div>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import antdTable from "../../../../PublicComponment/antdTable/antdTable";
import Cookies from "js-cookie";

export default {
  name: "createNewProject",

  components: {
    antdTable,
  },
  props: {
    tag_ipro: Array,
    tag_loc: Array,
  },
  data() {
    return {
      // firmiana数据
      // tag_ipro: [],
      // local upload 数据
      // tag_loc: [],
      // 自定义信息填入
      messageTagData: "",
      // 自定义tag数据分发给子组件暂存地址
      messageTags: [],

      // 提交数据
      input1: "",
      input2: "",
      projectInfo: {
        projectName: "",
        projectDescription: "",
        exps: [],
      },
    };
  },

  computed: {
    // 数据合并
    tags() {
      let arr = [...this.tag_ipro, ...this.tag_loc];
      return arr;
    },
  },
  created() {
    // this.tag_ipro = JSON.parse(localStorage.getItem("tag_ipro"));
    // this.tag_loc = JSON.parse(localStorage.getItem("tag_loc"));
  },
  watch: {
    // data() {
    //   this.tag_ipro = JSON.parse(localStorage.getItem("tag_ipro"));
    //   this.tag_loc = JSON.parse(localStorage.getItem("tag_loc"));
    // },
  },
  mounted() {
    if (sessionStorage.getItem("workflowState")) {
      let obj = JSON.parse(sessionStorage.getItem("workflowState"));
      this.workflowId = obj.workflowId;
      this.nodesData = obj.nodesData;
      this.$store.commit("getWorkflowId", obj.workflowId);
    }
  },

  methods: {
    // 获取组件中传递的firmiana实验数据
    tagData_ipro(e) {
      this.tag_ipro = e;
    },

    // 获取组件中传递的local upload 实验数据
    tagData_loc(e) {
      this.tag_loc = e;
    },

    // 处理自定义写入实验数据
    handleMessageData() {
      let arr = this.messageTagData.split(","); //字符分割
      arr.forEach((element) => {
        if (element === "" || this.messageTags.indexOf(element) > -1) {
          return;
        }
        this.messageTags.push(element);
      });
      // 输入完成之后清除输入框
      this.messageTagData = "";
    },

    handleClose(removedTag) {
      if (this.tag_ipro.indexOf(removedTag) > 1) {
        this.tag_ipro.splice(this.tag_ipro.indexOf(removedTag), 1);
      } else if (this.tag_loc.indexOf(removedTag) > 1) {
        this.tag_loc.splice(this.tag_loc.indexOf(removedTag), 1);
      }
    },

    // 使用delete按键删除事件
    handleMessageDelete() {
      if (this.messageTagData === "") {
        // 输入框中没有任何数据的时候，删除数组中的最后一个元素。改变原数组！
        this.messageTags.pop();
      }
    },

    // 点击聚焦事件
    getFocus() {
      this.$refs.message.focus();
    },

    // 提交
    submitNewProInfo() {
      this.projectInfo.projectName = this.input1;
      this.projectInfo.projectDescription = this.input2;
      if (
        this.projectInfo.projectName == "" &&
        this.projectInfo.projectName == " "
      ) {
        this.progressWdith = 0;
        this.$message.warning(
          "Please complete and correct the information.",
          5
        );
      } else if (this.tags.length == 0) {
        this.progressWdith = 0;
        this.$message.warning(
          "You have not selected any experiment number.",
          5
        );
      } else {
        this.projectInfo.exps = {
          iproteome: [],
          local: [],
        };
        this.tag_ipro.forEach((ele) => {
          this.projectInfo.exps.iproteome.push(ele.expname);
        });
        this.tag_loc.forEach((ele) => {
          this.projectInfo.exps.local.push(ele.expname);
        });

        let projectInfo = this.projectInfo;
        let iproteome_token = JSON.parse(Cookies.get("iproteome_token"));
        this.$ProjectModels
          .postProjectModels(projectInfo, iproteome_token)
          .then((res) => {
            this.$router.push({ path: "/projectSet" });
            let obj = {
              id: res.data.projectId,
              name: res.data.projectName,
            };
            this.$store.commit("getProjectId", obj);
            this.$message.success("Created successfully", 3);
            this.progressWdith = 100;
          })
          .catch((error) => {
            this.$message.error(
              "Failed to submit the new project. Please try again or check the network." +
                "error:" +
                error,
              5
            );
            this.progressWdith = 0;
          });
      }
    },

    progressBtn() {
      let progressnuw = 0;
      var timer = setInterval(() => {
        progressnuw = progressnuw + 2.5;
        if (progressnuw >= 100) {
          clearInterval(timer);
        }
        this.progressWdith = progressnuw;
      }, 1);
    },
  },
};
</script>

