<template>
  <div class="dataPage">
    <div class="header">
      <keep-alive>
        <analyNav @toDataPage="toDataPage" :currentTab="currentTab">
          <template v-slot:navWorkflow>
            <template v-for="items in navWorkflow">
              <div
                class="workflow-line"
                :key="items.sign"
                @click="toWorkflow(items.sign)"
              >
                &nbsp;&nbsp;<span class="workflow-text">{{ items.title }}</span>
                <template v-if="items.sign != navWorkflow.length">
                  <span>&nbsp;></span>
                </template>
              </div>
            </template>
          </template>
        </analyNav>
      </keep-alive>
    </div>
    <div class="content">
      <div class="content-title">
        <dataHeader @togglemainId="toggleMain" :mainId="mainId"></dataHeader>
      </div>
      <div class="main container" v-if="mainId == 0">
        <div class="data-local">
          <div class="data-controller">
            <a-button @click="createProject" type="primary" ghost>
              Create New Project
            </a-button>
          </div>
          <div class="box-title">Custom upload data display</div>
          <p style="font-style: italic; margin-bottom: 0.9375rem">
            If you check the table, the data will be displayed in the total set.
            You can remove the selected experiment by canceling the check or
            deleting it.
          </p>
          <antdTable
            @changeData="tagData_loc"
            :from="'local'"
            :messageTags="messageTags"
            key="local"
          ></antdTable>
        </div>
      </div>
      <div class="main container" v-if="mainId == 1">
        <!-- 展示firmiana数据 -->

        <div class="data-firmiana">
          <a-button
            style="float: right"
            @click="createProject"
            type="primary"
            ghost
          >
            Create New Project
          </a-button>
          <div class="box-title">Firmiana data display</div>
          <p style="font-style: italic; margin-bottom: 0.9375rem">
            If you check the table, the data will be displayed in the total set.
            You can remove the selected experiment by canceling the check or
            deleting it.
          </p>
          <antdTable
            @changeData="tagData_ipro"
            :from="'iproteome'"
            :messageTags="messageTags"
            key="iproteome"
          ></antdTable>
        </div>
      </div>

      <div class="main container" v-if="mainId == 2">
        <!-- 展示local数据 -->

        <div class="data-local">
          <a-button
            style="float: right"
            @click="createProject"
            type="primary"
            ghost
          >
            Create New Project
          </a-button>
          <div class="box-title">Custom upload data display</div>
          <p style="font-style: italic; margin-bottom: 0.9375rem">
            If you check the table, the data will be displayed in the total set.
            You can remove the selected experiment by canceling the check or
            deleting it.
          </p>
          <antdTable
            @changeData="tagData_loc"
            :from="'local'"
            :messageTags="messageTags"
            key="local"
          ></antdTable>
        </div>
      </div>

      <div class="main container" v-if="mainId == 3">
        <!-- 上传数据 -->
        <div class="data-local">
          <div class="box-title">Upload Data</div>
        </div>
      </div>
      <div v-if="mainId == 4">
        <!-- 创建新项目 -->
        <!-- <div class="box-title">Create New Project</div> -->
        <createNewProject
          :tag_ipro="tag_ipro"
          :tag_loc="tag_loc"
        ></createNewProject>
      </div>
    </div>
    <div class="footer">
      <analyFooter></analyFooter>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import analyNav from "../../../PublicComponment/analyNav";
import analyFooter from "../../../PublicComponment/analyFooter";
import dataHeader from "./component/dataHeader";
import antdTable from "../../../PublicComponment/antdTable/antdTable";
import createNewProject from "./component/createNewProject.vue";

export default {
  name: "dataPage",

  components: {
    analyNav,
    analyFooter,
    dataHeader,
    antdTable,
    createNewProject,
  },

  data() {
    return {
      // 页面切换标识
      currentTab: "dataPage",
      mainId: 0,
      // 表格只读
      messageTags: [],
      tag_ipro: [],
      tag_loc: [],
    };
  },

  computed: {
    ...mapState({
      projectId: (state) => state.projectId.proInfo.projectId,
      projectName: (state) => state.projectId.proInfo.projectName,
      navWorkflow: (state) => state.titleWorkflow.navWorkflow,
    }),
  },

  mounted() {
    if (sessionStorage.getItem("workflowState")) {
      let obj = JSON.parse(sessionStorage.getItem("workflowState"));
      this.workflowId = obj.workflowId;
      this.nodesData = obj.nodesData;
      this.$store.commit("getWorkflowId", obj.workflowId);
    }
  },

  methods: {
    // 获取组件中传递的firmiana实验数据
    toggleMain(e) {
      // window.console.log(e);
      this.mainId = e;
    },

    showFriData() {
      this.mainId = 1;
      // 网页点击追踪
      let arr = [];
      arr.push(this.navWorkflow[0]);
      let obj_0 = {
        title: "Data",
        sign: 2,
      };
      arr.push(obj_0);
      let obj_1 = {
        title: "Firmiana Data",
        sign: 3,
      };
      arr.push(obj_1);
      this.$store.commit("postNavWorkflowData", arr);
    },

    showCusData() {
      this.mainId = 2;
      // 网页点击追踪
      let arr = [];
      arr.push(this.navWorkflow[0]);
      let obj_0 = {
        title: "Data",
        sign: 2,
      };
      arr.push(obj_0);
      let obj_1 = {
        title: "Uploaded Data",
        sign: 3,
      };
      arr.push(obj_1);
      this.$store.commit("postNavWorkflowData", arr);
    },
    //传送数据到创建项目页面上
    createProject() {
      this.mainId = 4;
      let arr = [];
      arr.push(this.navWorkflow[0]);
      let obj_0 = {
        title: "Data",
        sign: 2,
      };
      arr.push(obj_0);
      let obj_1 = {
        title: "Create New Project",
        sign: 3,
      };
      arr.push(obj_1);
      this.$store.commit("postNavWorkflowData", arr);
    },

    // 获取组件中传递的firmiana实验数据
    tagData_ipro(e) {
      this.tag_ipro = e;
    },

    // 获取组件中传递的local upload 实验数据
    tagData_loc(e) {
      this.tag_loc = e;
    },
    toDataPage(e) {
      this.mainId = e;
    },
  },
};
</script>

